html {
 font-size: 16px;
 line-height: 1.5;
}

@media (min-width: 38rem) {
 html {
 font-size: 16px;
 }
}

@media (min-width: 46rem) {
 div.content {
 max-width: 50%;
 }
}

figure.highlight pre {
 overflow-x: auto;
 white-space: pre;
}

.sidebar-about h1 {
 font-family: 'Epilogue', sans-serif;
 font-size: 2.25rem;
}

.clearfix {
 clear: both
}

.example-embed {
 margin-bottom: 25px;
}

.fancylink {
 white-space: nowrap;
}

.fancylink svg {
 width: 8px;
 height: 10px;
}
